.users-type-action
  display: flex
  justify-content: space-between
  align-items: center
  flex-wrap: wrap

  .drop
    &-input
      height: 35px

.users-table
  max-width: 1170px
  thead
    th
      padding: 10px 15px
      +r(1399)
        padding: 10px
  tbody
    tr
      td
        font-size: 12px
        padding: 10px 15px
        word-break: break-all
        +r(1399)
          padding: 10px
        & > *
          word-break: break-all

    &.tb-sortable
      tr
        &.ui-sortable-helper
          max-width: 1170px
          td:nth-child(2)
            width: 100%
        td:first-child
          color: $accent
          cursor: grab

table.users-table tr th:last-child,
table.users-table tr td:last-child
  min-width: auto

.table-before-btn + table.users-table tr th.footable-last-visible
  padding-right: 65px !important

.user-info
  background: #F8F9FA
  padding: 30px 0 30px 30px

  .scroll-box
    max-height: 297px
    overflow: auto


.list-info-title
  display: block
  position: relatives
  margin-bottom: 5px

  font-style: normal
  font-weight: 400
  font-size: 14px
  line-height: 100%
  display: flex
  align-items: center
  letter-spacing: 0.01em
  color: #B4BACA



.id-adress
  cursor: pointer
  &.--locked
    display: flex
    align-items: center
    flex-direction: row-reverse
    color: #FF5F72
    +icon(locked)
    &::before
      display: inline-block
      width: 18px
      height: 18px
      flex: 0 0 18px
      margin-top: -3px
      margin-left: 12px
      font-size: 18px
      color: #FF5F72
  &.--unlocked
    display: flex
    align-items: center
    flex-direction: row-reverse
    color: #57CA70
    +icon(unlocked)
    &::before
      display: inline-block
      width: 18px
      height: 18px
      flex: 0 0 18px
      margin-top: -3px
      margin-left: 12px
      font-size: 18px
      color: #57CA70
      +tr(0.25s)

.table-before-btn.--users
  & > *
    right: 20px
    +r(1199)
      right: 10px

.user-password
  .show-password
    display: flex
    justify-content: center
    align-items: center
    width: 31px
    height: 31px
    flex: 0 0 31px
    background-color: #F8F9FA
    border-radius: 6px
    top: 7px
    right: 7px

.page-users-head
  display: flex
  margin-top: 15px
  margin-bottom: 30px
  align-items: center
  h4
    margin: 0
    margin-right: 15px

.select-rules
  display: inline-block
  border: 1.5px solid #E1E3EA
  border-radius: 6px
  background: #FFFFFF
  color: #C3C8D5
  font-weight: 100
  font-size: 14px
  height: 45px
  padding: 0 45px 0 15px
  overflow: hidden
  line-height: 45px
  text-overflow: ellipsis
  white-space: nowrap
  box-sizing: border-box
  padding-right: 34px
  background-image: url("data:image/svg+xml,%3Csvg width='20' height='20' viewBox='0 0 20 20' xmlns='http://www.w3.org/2000/svg'%3E%3Cpolyline fill='none' stroke='%23A3AED0' stroke-width='1.03' points='16 7 10 13 4 7'%3E%3C/polyline%3E%3C/svg%3E")
  background-position: right 10px center
  background-size: 15px
  background-repeat: no-repeat
  width: 100%
  text-align: left 

.user-wrapper 
  display: flex
  flex-wrap: wrap
    
  &__photo
    flex: 0 0 366px
    display: flex
    flex-wrap: wrap
    align-items: center
    flex-direction: column
  &__body
    padding-left: 30px
    flex: 0 0 calc(100% - 366px)

  +r(991)
    &__photo
      flex: 0 0 100%
      margin-bottom: 15px
      justify-content: center

    &__body
      padding-left: 0
      flex: 0 0 100%

  .user-reward
    display: inline-flex
    flex: 0 0 auto

.user-photo
  border-radius: 20px
  overflow: hidden
  margin-bottom: 20px
  width: 100%

  +r(991)
    max-width: 250px

  img
    display: block
    width: 100%

.child
  display: grid
  gap: 20px

  +rmin(1100)
    grid-template-columns: 87px 1fr 253px
    gap: 30px

.child-button
  background: #F0F1F4
  border-radius: 6px
  display: flex
  align-items: center
  justify-content: center
  min-height: 45px

  &__number
    font-weight: 500
    font-size: 16px
    line-height: 100%
    letter-spacing: 0.02em
    text-transform: uppercase
    color: #30393E
  &__remove
    width: 18px
    height: 18px
    margin-left: 5px
    display: flex
    justify-content: center
    align-items: center
    border-radius: 50%
    border: none
    padding: 0
    cursor: pointer
    padding-left: 1px
    +icon(close)
    background: #FF5F72
    +tr(0.25s)
    &::before
      color: #fff
      font-size: 8px
    +h
      background: #f00

.user-group
  margin-bottom: 37px

.user-page-bottom
  border-top: 1px solid #D2D6DF
  padding-top: 35px

.users
  +rmin(767)
    .add-social__item
      margin-bottom: 5px

.add-social__button
  .ico_plus
    &::before
      content: '+'
      font-weight: 700
      line-height: 1
      width: 35px
      flex: 0 0 35px
      height: 35px
      display: flex
      align-items: center
      justify-content: center
      border: 2px solid #628EFF
      border-radius: 14px
      color: #628EFF
      border-radius: 50%
      padding-bottom: 9px
      padding-left: 1px

.activity-filters
  display: grid
  gap: 20px
  margin-bottom: 30px
  grid-template-columns: 1fr

  +rmin(568)
    grid-template-columns: 1fr 1fr

  +rmin(1700)
    grid-template-columns: 1fr 1fr 1fr

  +r(1699)
    > *:last-child
      grid-column: span 2
  +r(567)
    > *:last-child
      grid-column: span 1


.activity-container
  max-height: 435px
  overflow: hidden

  table.footable tbody td
    height: 44px

  .table-td-small
    margin: 0

.status-button.status-button--sm
  padding: 5px 10px
  height: 24px

.activity
  &__date
    font-weight: 400
    font-size: 12px
    line-height: 100%
    color: #30393E
  &__time
    font-weight: 400
    font-size: 12px
    line-height: 100%
    color: #878C98
    margin-left: 5px

.activity-btn-full
  width: 100%
  display: block
  border: 0
  padding: 10px
  font-weight: 500
  font-size: 14px
  line-height: 100%
  letter-spacing: 0.02em
  color: #628EFF
  background: #EFF4FF
  text-align: center
  +tr(.25s)
  .--close
    display: none
  &.is-active
    .--active
      display: none
    .--close
      display: block
    
  +h
    background: #628EFF
    color: #EFF4FF

.user-quality
  display: block
  .password-quality__title
    margin-bottom: 10px
  .password-quality__list
    margin-left: 0


.confirm-date
  margin: 0
  font-weight: 400
  font-size: 14px
  line-height: 100%
  letter-spacing: 0.01em
  color: #30393E