
.storage-wrap
  +rmin(768)
    display: flex
    flex-wrap: wrap

.storage-sidebar
  margin-bottom: 20px
  +rmin(768)
    margin-bottom: 0
    flex: 0 0 370px
    padding-right: 29px
    border-right: 1px solid #E1E3EA
    position: relative
    &::after
      width: calc( 100% - 4px )
      height: 50px
      background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #FFFFFF 100%)
      position: absolute
      left: 0
      bottom: 0
      content: ''
    +r(1199)
      padding-right: 15px
    +r(991)
      flex: 0 0 300px

.storage-content
  +rmin(768)
    flex: 0 0 calc(100% - 370px)
    padding-left: 30px
    +r(1199)
      padding-left: 15px
    +r(991)
      flex: 0 0 calc(100% - 300px)

.storage-menu-container
  position: relative
  max-height: 701px
  overflow-y: auto
  margin-right: -30px
  padding-right: 30px
  scrollbar-color: #C3C8D5 transparent
  scrollbar-width: thin!important
  -moz-appearance: none !important
  +r(1359)
    max-height: calc(100vh - 430px)
  +r(1199)
    margin-right: -16px
    padding-right: 16px
  +r(767)
    max-height: 225px

  &::-webkit-scrollbar
    width: 4px

  &::-webkit-scrollbar-track
    background-color: transparent

  &::-webkit-scrollbar-thumb
    background-color: #C3C8D5

.storage-menu
  margin: 0
  padding: 0
  list-style: none
  &__item
    position: relative
    display: flex
    flex-wrap: wrap
    align-items: center
    &.is-active
      .storage-menu__toggle
        &::before, &::after
          transform: translate(-50%,-50%)
          background: #628EFF
      .storage-menu__link
        color: #30393E
        .product-storage__icon
          color: #30393E

  &__link
    font-weight: 500
    font-size: 14px
    line-height: 100%
    color: #878C98
    flex: 0 0 100%
    display: flex
    align-items: center
    +tr(.25s)
    margin-bottom: 10px
    +h
      color: #30393E
      .product-storage__icon
        color: #30393E

  &__toggle
    position: absolute
    right: -9px
    top: 0
    width: 30px
    height: 35px
    border: 0
    outline: 0
    padding: 0
    &::after, &::before
      background: #C0D2FF
      width: 12px
      height: 2px
      position: absolute
      left: 50%
      top: 50%
      transform: translate(-50%,-50%)
      content: ''
      +tr(.25s)
      +h
        background: #628EFF
    &::before
      transform: translate(-50%,-50%) rotate(90deg)

.product-storage__icon
  background: #F8F9FA
  border-radius: 6px
  width: 45px
  height: 45px
  flex: 0 0 45px
  display: flex
  align-items: center
  justify-content: center
  font-size: 32px
  margin-right: 12px
  +tr(.25s)
  color: #30393E

  &.storage-menu__icon
    color: #878C98
    width: 35px
    height: 35px
    font-size: 25px
    flex: 0 0 35px

.storage-sub-menu-box
  display: none
  flex: 0 0 100%
  &.is-show
    display: block

.storage-sub-menu
  margin: 0
  padding: 0 0 0 67px
  list-style: none
  +r(991)
    padding: 0 0 0 47px

  &__item
    margin-bottom: 10px
    line-height: 12px
  &__name
    font-weight: 500
    font-size: 12px
    line-height: 100%
    color: #5A5D65
    margin-bottom: 10px
    display: inline-block
    +tr(.25s)
    +h
      color: #30393E

.storage-inner-menu 
  margin: 0
  padding: 0 0 0 10px
  list-style: none
  display: none
  &.is-show
    display: block
  &__item
    margin-bottom: 10px
    line-height: 12px
  &__link
    font-weight: 400
    font-size: 12px
    line-height: 100%
    color: #878C98
    position: relative
    display: flex
    flex-wrap: wrap
    justify-content: space-between
    +h
      color: #30393E
      &::after
        opacity: 1
    &.is-active
      color: #30393E
      &::after
        opacity: 1
    &::after
      width: 4px
      height: 4px
      content: ''
      background: #628EFF
      position: absolute
      left: -9px
      top: 4px
      +tr(.25s)
      opacity: 0

.storage-recommendation
  .storage-inner-menu__name
    flex: 0 0 calc(100% - 85px)

.storage-write-off
  .ico_info
    height: 35px
    padding: 0 10px
    &::before
      margin-top: -1px
      +tr(.25s)
      font-size: 21px
      margin-right: 5px

.amount-counter
  border: 0
  max-width: 119px
  margin: 0 auto
  display: inline-flex

  .counter__field input
    width: 49px
    flex: 0 0 49px
    height: 35px

  .counter__btn
    border: 0
    background: #EFF4FF
    border-radius: 6px
    flex: 0 0 35px
    color: #628EFF
    font-size: 20px
    height: 35px
    &::after
      display: none

.amount
  font-size: 20px 
  font-weight: 500

.storage-info
  padding: 30px
  background: #F8F9FA
  &__row
    margin-bottom: 15px
    &:last-child
      margin-bottom: 0
  &__label
    font-weight: 500
    font-size: 12px
    line-height: 100%
    color: #B4BACA
    margin-bottom: 5px
  &__data
    font-weight: 400
    font-size: 14px
    line-height: 100%
    color: #30393E

.recomendation-th
  display: flex
  margin-bottom: 10px
  &__item 
    font-weight: 500
    font-size: 9px
    line-height: 100%
    color: #B4BACA
    margin-left: 5px
    flex: 0 0 40px

    &:first-child
      margin-left: auto

.recomendation-td
  display: flex
  flex: 0 0 85px
  margin-left: auto
  &__item 
    margin-left: 5px
    flex: 0 0 40px

    &:first-child
      margin-left: 0

.storage-table
  td:empty
    display: none
  &.footable tbody td
    font-size: 12px
  &.footable tbody tr.comment-row td
    padding-top: 3px
    +rmin(1200)
      height: 22px
  &.footable tbody tr.comment-row td:first-child
    padding-left: 65px  
    +r(1200)
      padding-left: 45px
    +r(568)
      padding-left: 10px
  +r(1500)
    &.footable thead tr th
      padding: 12px 5px
  +r(1200)
    &.footable tbody tr.comment-row + .footable-detail-row
      th 
        display: none!important
      .comment-row:not(:first-child)
        display: none!important
      .comment-row td
        padding-left: 45px!important
  +r(568)
    &.footable tbody tr.comment-row + .footable-detail-row
      .comment-row td
        padding-left: 10px!important
    .comment-label
      display: block
      flex: 0 0 100%
      margin-bottom: 5px
    .comment-data
      margin-left: 0


.incoming-container
  display: flex
  flex-wrap: wrap
  &__left
    flex: 0 0 270px
  &__right
    padding-left: 30px
    flex: 0 0 calc( 100% - 270px )
    min-width: 0
  +r(767)
    &__left
      margin-bottom: 20px
    &__right
      padding-left: 0
      flex: 0 0 100%

.comment-label
  font-weight: 500
  font-size: 12px
  line-height: 100%
  color: #B4BACA

.comment-data
  font-weight: 400
  font-size: 12px
  line-height: 100%
  color: #30393E
  margin-left: 10px

.purchasing
  background: #F8F9FA
  padding: 20px 20px 10px
  margin-bottom: 30px

  +rmin(568)
    display: flex
    flex-wrap: wrap

  +rmin(1023)
    padding: 30px 30px 20px

.purchasing-item
  margin-right: 25px
  margin-bottom: 10px

  +rmin(1023)
    margin-right: 60px

  &:last-child
    margin-right: 0

  &__label
    font-weight: 500
    font-size: 12px
    line-height: 100%
    color: #B4BACA
    margin-bottom: 5px
  &__data
    font-weight: 400
    font-size: 14px
    line-height: 100%
    color: #30393E

.storage-purchasing-inner
  .js-table
    span 
      font-size: 12px