.brand-list
  flex: 0 0 100%
  margin: 0
  padding: 0 0 0 40px
  list-style: none
  min-width: 0

  +r(767)
    margin-top: 5px

  &__item
    max-width: 313px
    width: 100%
    background: #F8F9FA
    padding: 5px 10px
    align-items: center
    display: flex

    &:nth-child(even)
      background: #fff
      
  &__name
    font-weight: 400
    font-size: 12px
    line-height: 100%
    color: #30393E
    flex: 0 0 calc(100% - 88px)
    white-space: nowrap
    text-overflow: ellipsis
    overflow: hidden
  &__percent
    font-weight: 500
    font-size: 12px
    line-height: 100%
    color: #30393E
    flex: 0 0 70px
  &__remove
    width: 18px
    height: 18px
    flex: 0 0 18px
    margin-left: 5px
    display: flex
    justify-content: center
    align-items: center
    border-radius: 50%
    border: none
    padding: 0
    cursor: pointer
    padding-left: 1px
    +icon(close)
    background: #FF5F72
    +tr(0.25s)
    &::before
      color: #fff
      font-size: 8px
    +h
      background: #f00

.custome-table-discount
  .toggle-dropdown
    flex-wrap: wrap
    min-width: 0

    .brand-list
      padding: 0 0 0 40px!important
      +r(1199)
        padding: 0 0 0 32px!important
      +r(767)
        padding: 0 0 0 0!important


  .custome-table__cell
    flex-wrap: wrap

    .brand-list
      padding: 0 0 0 124px
      +r(1199)
        padding: 0 0 0 72px
      +r(767)
        padding: 0

  .custome-table__cell 
    min-height: 35px
    .flag
      margin-left: 0
  .custome-table__cell-group
    align-items: start

  .custome-table__cell input.form-control
    height: 35px
  .custome-table__head
    .custome-table__cell-group .custome-table__cell
      border-left: 1px solid transparent
